<template>
    <div>
        <div class="doc-left">
            <ul class="header">
                <li>
                    <h3>基础</h3>
                    <ul>
                        <router-link to="#start" tag="li">开始</router-link>
                        <router-link to="#dogtai" tag="li">动态路由</router-link>
                        <router-link to="#qiantao" tag="li">嵌套路由</router-link>
                    </ul>
                </li>
                <li>
                    <h3>进阶</h3>
                    <ul>
                        <router-link to="#dhgz" tag="li">导航钩子</router-link>
                        <router-link to="#lyyxx" tag="li">路由元信息</router-link>
                        <router-link to="#gddx" tag="li">过渡动效</router-link>
                    </ul>
                </li>
            </ul>
            <div class="doc-right">
                <span>基础</span>
                <hr>
                <h3><a id="start" href="#start">开始信息</a></h3>
                <div>开始的信息内容</div>
                <h3><a id="dogtai" href="#dogtai">动态路由</a></h3>
                <div>动态路由的内容</div>
                <h3><a id="qiantao" href="#qiantao">嵌套路由</a></h3>
                <div>嵌套路由的内容</div>
                <span>进阶</span>
                <hr>
                <h3><a id="dhgz" href="#dhgz">导航钩子</a></h3>
                <div>开始的信息内容</div>
                <h3><a id="lyyxx" href="#lyyxx">路由元信息</a></h3>
                <div>开始的信息内容</div>
                <h3><a id="gddx" href="#gddx">过渡动效</a></h3>
                <div>开始的信息内容</div>
            </div>
        </div>
    </div>
</template>
<script>
import TWEEN from 'tween.js'
export default {
    beforeRouteEnter(to,from,next){
        next((vm)=>{
            vm.animate(to)// 进入页面就执行函数
        })
    },
    beforeRouteUpdate(to,from,next){
        // console.log(to)
        this.animate(to)
        next();
    },
    methods:{
        animate(to){
            function animateF(time){//如果time没有被指定当使用当前时间
                requestAnimationFrame(animateF)
                TWEEN.update(time)
            }
            if(to.hash){
                var el = document.getElementById(to.hash.slice(1))
                var doc = document.getElementsByClassName('doc-right')[0]
                console.log(to.hash)
                if(el){
                    animateF()
                    new TWEEN.Tween({ //起始位子到结束位置过渡动画
                        number:doc.scrollTop //容器的起始位置，number是一个自定义的属性
                    })
                      .to({
                          number:el.offsetTop //目标区域元素的offsettop
                      },500)//过渡时间
                      .onUpdate(function(){
                          doc.scrollTop = this.number.toFixed(0)//这个this指的是TWEEN.Tween这个创建的实例，也就是传入给TWEEN.Tween中的number的值
                      })
                      .start()
                }
            }
            
        }
    }
}
</script>

<style scoped>
    .is-active{
        color:lightblue;
    }
    body{
        position:relative;
        width:100%;
        height:100%;
        margin:0;
    }
    .doc-left{
        border:1px solid red;
    }
    ul{
        padding:0;margin:0;
        list-style:none;
    }
    .header{
        position:absolute;
        left:0;
        width:20%;
        height:100%;
        border:1px solid green;
        /* padding-left:50px; */
        text-indent:30px;
        float:left;
    }
    .header li ul{
        text-indent:50px;
    }
    .header li ul li{
        margin-bottom:20px;
        font-style:arial;
    }
    .doc-right{
        position:absolute;
        width:80%;
        height:100%;
        left:20%;
        float:left;
        border:1px solid #000 ;
        background-color:lavender;
        overflow:hidden;
    }
    .doc-right h3{
        padding:0;margin:0;
    }
    .doc-right h3 a{
        text-decoration:none;
    }
    .doc-right div{
        margin-bottom:500px;
    }
</style>



